<template>
  <ul>
    <template v-for="x in listOfCategories" :key="x.postCategoryId">
      <li class="form-check">
        <input type="checkbox" :value="x.postCategoryId"
               v-model="localValueT"
               @change="onModelValueUpdate"
               class="form-check-input"
               :id="`category-checkbox-with-id-${x.postCategoryId}`"
        />

        <label class="form-check-label"
               :for="`category-checkbox-with-id-${x.postCategoryId}`"
        >
          {{ x.categoryName }}
        </label>
      </li>

      <categories-check-list v-model="localValueT"
                             @update:modelValue="onModelValueUpdate"
                             :children="true"
                             :categories="x.children"
                             @delete="$emit('delete',$event)"
                             @addChild="$emit('addChild',$event)"
                             @update="$emit('update',$event)"
      />
    </template>
  </ul>
</template>

<script>
export default {
  name: 'CategoriesCheckList',
  props: {
    modelValue: { default: () => ([]) },
    categories: { default: undefined },
    children: { default: false }
  },
  data: () => ({
    localValue: {},
    working: false,
    localCategories: undefined
  }),
  methods: {
    retrieveCategories() {
      this.working = true
      this.$fetch('/postcategory', 'get')
          .then(x => {
            this.localCategories = x.data
          })
          .finally(() => this.working = false)
    },
    onModelValueUpdate() {
      /*const res = { ...this.modelValue, ...this.localValue }
      this.$emit(
          'update:modelValue',
          Object.keys(res)
      )*/
    },
  },
  computed: {
    listOfCategories() {
      if (this.categories) {
        return this.categories
      }

      if (this.localCategories) {
        return this.localCategories.filter(x => x.parentId === null)
      }

      return []
    },
    localValueT: {
      get() {
        return this.modelValue ? this.modelValue : []
      },
      set(nw) {
        this.$emit('update:modelValue', nw)
      }
    }
  },
  watch: {
    value() {

    }
  },
  mounted() {
    if (!this.categories && !this.children) {
      this.retrieveCategories()
    }
  }
}
</script>

