export default {
  methods: {
    tinymceLoad(id, imageCallback = null) {
      const exstenc = window.tinymce.get(id)
      if (exstenc) {
        exstenc.destroy()
      }
      window.tinymce.init({
        selector: `#${id}`,

        directionality: 'rtl',
        plugins: ['directionality', 'image', 'fullscreen', 'link', 'lists', 'advlist',
          'media', 'anchor', 'code', 'table'],
        toolbar: [
          'undo redo | styleselect | bold italic | alignleft aligncenter alignright alignjustify | outdent indent | rtl ltr ' +
          ' numlist bullist | ' +
          ' | filemanagerToolbarButton mhr_clear_fonts | media ' +
          ' link anchor fullscreen | code',
          'addImageButton'
        ],
        table_toolbar: 'tableprops tabledelete | tableinsertrowbefore tableinsertrowafter tabledeleterow | tableinsertcolbefore tableinsertcolafter tabledeletecol',

        content_css: false,
        content_style: 'body : {font-family: vazir}',
        font_formats: '',

        convert_urls: false,
        relative_urls: false,
        remove_script_host: false,

        min_height: 350,

        setup: editor => {
          editor.ui.registry.addButton('addImageButton', {
            text: 'image',
            onAction: () => {
              if (imageCallback) {
                imageCallback(editor)
              }
            }
          })
        }
      })
    },
    getContent(id) {
      return window.tinymce.get(id)
        .getContent()
    },
    setContent(id, content) {
      return window.tinymce.get(id)
        .setContent(content)
    },
  }
}
